html {
  scroll-behavior: smooth;
}

*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

input, select, button, textarea {
  font: inherit;
}

img, svg, picture, video {
  display: block;
  max-width: 100%;
}

ul, ol {
  list-style: none;
}

body {
  min-height: 100vh;
}

body {
  font-family: "Hanken Grotesk", sans-serif;
  font-size: 1rem;
  line-height: 1.303125rem;
}
@media (min-width: 40rem) {
  body {
    font-size: 1.125rem;
    line-height: 1.465625rem;
  }
}

.font-xl {
  font-size: 3.5rem;
  line-height: 4.5rem;
  font-weight: 800;
}
@media (min-width: 40rem) {
  .font-xl {
    font-size: 4.5rem;
  }
}

.font-l {
  font-size: 1.5rem;
  line-height: 1.954375rem;
}
@media (min-width: 40rem) {
  .font-l {
    font-size: 2rem;
    line-height: 2.60625rem;
  }
}

.font-m {
  font-size: 1.125rem;
  line-height: 1.465625rem;
}
@media (min-width: 40rem) {
  .font-m {
    font-size: 1.5rem;
    line-height: 1.954375rem;
  }
}

a {
  text-decoration: none;
}

.container {
  max-width: 46rem;
  margin: 0 auto;
}

.component {
  background-color: hsl(0, 0%, 100%);
}
.component__result {
  background: linear-gradient(180deg, #7755ff 0%, #6943ff 0.01%, #2f2ce9 100%);
  border-radius: 0 0 2rem 2rem;
  padding: 1.5rem 3.625rem 2.5rem;
  color: hsl(241, 100%, 89%);
  text-align: center;
}
@media (min-width: 40rem) {
  .component__result {
    padding: 2.375rem 3.375rem 2.875rem;
  }
}
.component__result-title {
  margin-bottom: 1.5rem;
}
@media (min-width: 40rem) {
  .component__result-title {
    margin-bottom: 2.1875rem;
  }
}
.component__result-value {
  background: linear-gradient(180deg, #4d21c9 0%, rgba(37, 33, 201, 0) 100%, rgba(37, 33, 201, 0) 100%);
  padding: 1.5625rem 2.375rem 1.375rem;
  width: fit-content;
  margin: 0 auto;
  border-radius: 50%;
  font-weight: 700;
  margin-bottom: 1.5rem;
}
@media (min-width: 40rem) {
  .component__result-value {
    padding: 3.4375rem 3.75rem 2.875rem;
    margin-bottom: 1.75rem;
  }
}
.component__result-value span {
  display: block;
  color: hsl(0, 0%, 100%);
}
.component__result-message {
  color: hsl(0, 0%, 100%);
  font-weight: 700;
  margin-bottom: 0.5rem;
}
@media (min-width: 40rem) {
  .component__result-message {
    margin-bottom: 0.875rem;
  }
}

body {
  background-color: hsl(221, 100%, 96%);
}